import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class RotHelper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      userStr: "",
      currentStr: "",
      letterShiftArray: [],
      globalOffset: 0
    }
  }

  render() {
    return (
      <div className="rot-helper-app">
        <div className="rot-helper-title">
          <RHTitle />
        </div>
        <div className="rot-helper-input">
          <RHInput handleChange={(event) => this.handleChange(event)} />
        </div>
        <div className="rot-helper-controls">
          <RHControl />
        </div>
        <div className="rot-helper-output">
          {this.createLetters(this.state.currentStr)}
        </div>
      </div>
    );
  }

  createLetters(string) {
    let letters = []
    for (let i = 0; i < string.length; i++) {
      letters.push(<RHLetter 
        value={string[i]} 
        key={i} 
        index={i}
        handleRotate={this.handleRotate.bind(this)}
        offset={this.state.letterShiftArray[i]} />)
    }
    return letters;
  } 

  handleChange(event) {
    let str = event.target.value;
    let lsa = this.state.letterShiftArray;
    for (let i = 0; i < str.length; i++) {
      if (lsa[i] === undefined) {
        lsa[i] = 0;
      }
    }
    this.setState({
      userStr: str,
      letterShiftArray: lsa,
      currentStr: this.applyOffset(str, lsa)
    });
  }

  handleRotate(index, num) {
    let lsa = this.state.letterShiftArray;
    lsa[index] += num;
    this.setState ({
      letterShiftArray: lsa,
      currentStr: this.applyOffset(this.state.userStr, lsa)
    });
  }

  applyOffset(str, lsa) {
    let currentStr = "";
    for (let i = 0; i < str.length; i++) {
      currentStr += String.fromCharCode(str.charCodeAt(i) + lsa[i]);
    }
    return currentStr;
  }
}

function RHTitle() {
  return (
    <h1>ROT Helper</h1>
  )
}

class RHInput extends React.Component {

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <textarea 
          value={this.props.value}
          onChange={this.props.handleChange} 
          placeholder='Enter text to rotate' />
      </form>
    )
  }
}

class RHLetter extends React.Component {

  render() {
    return (
      <div className="rot-letter">
        <button onClick={() => this.props.handleRotate(this.props.index, 1)}>
          /\
        </button>
        <p>
          {this.props.value}
        </p>
        <button onClick={() => this.props.handleRotate(this.props.index, -1)}>
          \/
        </button>
        <p>
          {this.props.offset}
        </p>
      </div>
    )
  }
}

class RHControl extends React.Component {
  render() {
    return (
    <div className="rot-letter">
      <button onClick={() => {}}>
        /\
      </button>
      <button onClick={() => {}}>
        \/
      </button>
    </div>
  )
  }
}


// ========================================

ReactDOM.render(
  <RotHelper />,
  document.getElementById('root')
);

  


